.container {
  margin-top: 1%;
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.1);
  border-radius: 16px;
  padding: 1%;
  margin: 1%;
  min-height: 50vh;
}

.slotText {
  font-size: 35px;
  color: #ffcc01;
  -webkit-text-stroke: 1.5px #365fac;
  font-weight: 750;
  margin: 1%;
}

.slots {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(5, 48px);
  grid-auto-rows: 48px;
  margin-top: 3%;
}

.slot1X1 {
  background-color: #efefef;
  margin: 1%;
}

.slot2X2 {
  background-color: #efefef;
  margin: 1%;
  grid-column: span 2;
  grid-row: span 2;
}

.buttonMoney {
  display: flex;
  width: 100%;
  height: 50px;
  justify-content: center;
  border-radius: 10px;
  border: solid 2px #365fac;
  margin-top: 5%;
}

.buttonMoney:hover {
  background: rgba(0, 0, 0, 0);
  color: #3a7999;
  box-shadow: inset 0 0 0 3px #3a7999;
}

.img {
  width: 30px;
  height: 30px;
  padding-top: 2.5%;
}
